<template>
  <van-tabbar route v-model="active" active-color="#87cefa" inactive-color="#000"  fixed safe-area-inset-bottom>
    <van-tabbar-item
      v-for="(item, index) in list"
      :key="index"
      replace
      :dot="((newMsg.length > 0 && index === 0) || (newFir && index === 1)) ? true :false "
      :to="item.path"
    >
      <span>{{item.title}}</span>
      <img slot="icon" slot-scope="props" :src="props.active ? item.active : item.inactive" />
    </van-tabbar-item>
  </van-tabbar>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { Toast } from 'vant';
export default {
  name: "tabbar",
  data() {
    return {
      active: 0,
      list: [
        {
          title: "聊天",
          path: "/chat",
          active: require("@/assets/tabbar/chatactive.png"),
          inactive: require("@/assets/tabbar/chat.png")
        },
        {
          title: "联系人",
          path: "/lianxi",
          active: require("@/assets/tabbar/lianxiactive.png"),
          inactive: require("@/assets/tabbar/lianxi.png")
        },
        {
          title: "服务",
          path: "/service",
          active: require("@/assets/tabbar/serviceactive.png"),
          inactive: require("@/assets/tabbar/service.png")
        },
        {
          title: "我的",
          path: "/mine",
          active: require("@/assets/tabbar/mineactive.png"),
          inactive: require("@/assets/tabbar/mine.png")
        }
      ]
    };
  },
  computed: {
    ...mapState(["userInfo","newMsg","newFir"]),
  },
  methods: {
    ...mapMutations(['clearNewMsg','addNewMsg','changMsg']),
  },
  mounted() {}
};
</script>

<style scoped>
.active_tab img {
  width: 26px;
  height: 26px;
}

.van-tabbar-item--active {
  color: #e10f02;
}
</style>