<template>
  <div id="app">
    <navbar
      v-if="$route.meta.showHead"
      class="navbar_color"
      :title="topLitle"
      :arrow="$route.meta.arrow"
      :right="$route.meta.right"
      :onClickRight="godo"
      :onClickLeft="goback"
    ></navbar>
    <transition name="fade" mode="out-in" v-if="$route.meta.keepAlive">
      <keep-alive>
        <router-view tag="div" v-if="$route.meta.keepAlive" class="content_app" />
      </keep-alive>
    </transition>
    <transition name="fade" mode="out-in" v-if="!$route.meta.keepAlive">
      <router-view tag="div" v-if="!$route.meta.keepAlive" class="content_app" />
    </transition>
    <tabbar v-if="$route.meta.showTab"></tabbar>
  </div>
</template>

<script>
import tabbar from "@/components/tabbar/tabbar";
import navbar from "@/components/navbar/navbar";
import { userInfo } from 'vuex'
export default {
  name: "App",
  components: {
    tabbar,
    navbar
  },
  data() {
    return {};
  },
  computed: {
    topLitle() {
      if(this.$route.path === '/chatinfo') {
        return this.$route.query.id.name
      } else {
        return this.$route.meta.title
      }
    }
    
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    godo() {
      if(this.$route.path === '/lianxi') {
        this.$router.push('/addFriend')
      }
    }
  },
  mounted() {},
};
</script>

